import React, { useState, useEffect } from 'react';
import PropTypes from "prop-types";
import { platform, IOS, Group, Cell } from "@vkontakte/vkui";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import HeaderButton from "@vkontakte/vkui/dist/components/HeaderButton/HeaderButton";
import Icon28ChevronBack from "@vkontakte/icons/dist/28/chevron_back";
import Icon24Back from "@vkontakte/icons/dist/24/back";

import Board from "./TikToe/TikToeCommon";
import "./Styles/TikToe.css";

const osName = platform();

const TikToe = props => {

  const state = {
    history: [{
      squares: Array(9).fill(null),
    }],
    xIsNext: true,
  }

  const history = state.history;
  const current = history[history.length - 1];
  const winner = calculateWinner(state.squares, state.xIsNext);
  
  const moves = history.map((step, move) => {
    const desc = move ?
      'Перейти к ходу #' + move :
      'К началу игры';
    return (
      <li>
        <button onClick={() => this.jumpTo(move)}>{desc}</button>
      </li>
    );
  });

	let status;
	if (winner) {
		status = 'Выиграла ' + winner;
	} else {
		status = 'Следующий ход: ' + (this.state.xIsNext ? 'Новая Республика' : 'Галактическая Империя');
  }

  return (
  <Panel id={props.id}>
    <PanelHeader
      left={
        <HeaderButton onClick={props.go} data-to="home">
          {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
        </HeaderButton>
      }
	>
	Звездные Нолики
	</PanelHeader>

    <Group>
      <Cell>
        <div className="game">
          <div className="game-board">
          <Board
          squares={current.squares}
          onClick={(i) => this.handleClick(i)}
        />
          </div>
          <div className="game-info">
          <div>{status}</div>
          <ol>{moves}</ol>
          </div>
        </div>
      </Cell>
    </Group>
  </Panel>)
    };

TikToe.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};

function calculateWinner(squares, xIsNext) {
	const lines = [
	  [0, 1, 2],
	  [3, 4, 5],
	  [6, 7, 8],
	  [0, 3, 6],
	  [1, 4, 7],
	  [2, 5, 8],
	  [0, 4, 8],
	  [2, 4, 6],
	];
	for (let i = 0; i < lines.length; i++) {
	  const [a, b, c] = lines[i];
	  if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
		return xIsNext ? 'Галактическая Империя' : 'Новая Республика';
	  }
	}
	return null;
  }


export default TikToe;
